.Header {
    display: grid;
    grid-template-columns: auto auto auto;
    align-items: center;
    justify-content: space-between;
    padding: 17px 32px;
    height: 96px;
    nav > .HeaderNavLinks {
        display: grid;
        grid-template-columns: auto auto auto auto auto;
        list-style-type: none;
        gap: 24px;
        font-weight: 500;
        font-size: 16px;
    }
    .CTAButton {
        background: none;
        border: none;
        background-color: black;
        color: white;
        font-weight: 700;
        font-size: 16px;
        width: 100px;
        height: 48px;
        border-radius: 32px;
    }
}